.header {
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  padding-top: 45px;
  padding-bottom: 41px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 685px) {
  .header {
    padding-top: 28px;
    padding-bottom: 31.6px;
  }
}
