.profile__name {
  width: 294px;
  margin: 0 18px 0 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 850px) {
  .profile__name {
    width: 189px;
    font-size: 27px;
    line-height: 1.22;
    margin-right: 10px;
  }
}
